<template>
  <AppCardLayout>
    <AppCardGroup>
      <AppProductsGrid
        v-if="orderData"
        :items="orderData.products"
        mode="moder"
        @update="onProductsUpdated"
      />
    </AppCardGroup>

    <AppCardGroup>
      <OrderFormRo
        v-if="orderData"
        :data="(orderData as any)"
      />
    </AppCardGroup>

    <AppCardGroup>
      <AppOrderPublicStatusForm
        v-if="orderPublicStatusDisplayData"
        :id="orderId"
        :data="orderPublicStatusDisplayData"
        :readonly="false"
        @update="onPublicStatusUpdate"
      />
    </AppCardGroup>

    <AppCardGroup>
      <AppOrderPrivateStatusForm
        v-if="orderPrivateStatusDisplayData"
        :id="orderId"
        :data="orderPrivateStatusDisplayData"
        :readonly="false"
        @update="onPrivateStatusUpdate"
      />
    </AppCardGroup>

    <AppCardGroup>
      <OrderDiscussion
        :order-id="orderId"
        mode="moder"
      />
    </AppCardGroup>
  </AppCardLayout>
</template>

<script setup lang="ts">
import AppCardGroup from '@/components/markup/AppCardGroup.vue';
import AppCardLayout from '@/components/markup/AppCardLayout.vue';
import OrderDiscussion from '../user/components/order-discussion.vue';

import OrderFormRo from '../user/components/order-form-ro.vue';
import AppOrderPublicStatusForm, {
  OrderPublicStatusDisplayData,
  OrderPublicStatusUpdateData,
} from '@/components/app/AppOrderPublicStatusForm.vue';
import AppOrderPrivateStatusForm, {
  OrderPrivateStatusDisplayData,
  OrderPrivateStatusUpdateData,
} from '@/components/app/AppOrderPrivateStatusForm.vue';
AppOrderPrivateStatusForm;
import {useRoute} from 'vue-router';
import {ref} from 'vue';
import {SupabaseService} from '@/services/supabase.service';
import {
  ModerOrderDisplayItem,
  ModerProduct,
  OrderListService,
} from '@/services/order-list.service';

const route = useRoute();
const queryParams = route.query;
const orderIdRaw = queryParams['orderId'];
const orderId = Number(Array.isArray(orderIdRaw) ? orderIdRaw[0] : orderIdRaw);
const orderData = ref(null as null | ModerOrderDisplayItem);
const orderPublicStatusDisplayData = ref(
  null as null | OrderPublicStatusDisplayData,
);
const orderPrivateStatusDisplayData = ref(
  null as null | OrderPrivateStatusDisplayData,
);
const supabase = useService(SupabaseService);
const service = useService(OrderListService);

async function refresh() {
  orderData.value = null;
  orderPublicStatusDisplayData.value = null;
  orderPrivateStatusDisplayData.value = null;
  const rows = await service.getAllOrders(orderId);
  const value = rows[0];
  orderData.value = value as any;

  const {
    orderStatusTypeId,
    productsPriceToPay,
    totalPriceToPay,
    shippingPriceToPay,
    deliveryDate,
    shippingDate,
    notes,
    totalPriceNet,
    totalPriceGross,
  } = value;

  orderPublicStatusDisplayData.value = {
    orderStatusTypeId,
    productsPriceToPay,
    totalPriceToPay,
    shippingPriceToPay,
  };

  orderPrivateStatusDisplayData.value = {
    deliveryDate,
    shippingDate,
    notes,
    totalPriceNet,
    totalPriceGross,
  };
}

onMounted(refresh);

async function onPublicStatusUpdate(data: OrderPublicStatusUpdateData) {
  if (!orderData.value) {
    return;
  }

  const {id, orderPrivateStatus, orderPublicStatus} = orderData.value;
  const {orderStatusTypeId, shippingPriceToPay} = data;

  await supabase.set(async x =>
    x.rpc('updateOrderStatus', {
      orderId: id,
      newTypeId: orderStatusTypeId,
      newPublicStatus: {shippingPriceToPay},
      newProductsPublicStatus: (orderPublicStatus as any).productsPublicStatus,
      newPrivateStatus: (orderPrivateStatus as any).privateStatus,
      newProductsPrivateStatus: (orderPrivateStatus as any)
        .productsPrivateStatus,
    }),
  );

  await refresh();
}

async function onPrivateStatusUpdate(data: OrderPrivateStatusUpdateData) {
  if (!orderData.value) {
    return;
  }

  const {id, orderPrivateStatus, orderPublicStatus, orderStatusTypeId} =
    orderData.value;

  await supabase.set(async x =>
    x.rpc('updateOrderStatus', {
      orderId: id,
      newTypeId: orderStatusTypeId,
      newPublicStatus: (orderPublicStatus as any).publicStatus,
      newProductsPublicStatus: (orderPublicStatus as any).productsPublicStatus,
      newPrivateStatus: data as any,
      newProductsPrivateStatus: (orderPrivateStatus as any)
        .productsPrivateStatus,
    }),
  );

  await refresh();
}

async function onProductsUpdated(products: ModerProduct[]) {
  if (!orderData.value) {
    return;
  }

  const {id, orderPrivateStatus, orderPublicStatus, orderStatusTypeId} =
    orderData.value;

  const newProductsPublicStatus = [] as any[];
  const newProductsPrivateStatus = [] as any[];

  for (const product of products) {
    const {priceToPay, priceGross, priceMarkup, priceNet} = product;
    newProductsPublicStatus.push({priceToPay});
    newProductsPrivateStatus.push({priceGross, priceMarkup, priceNet});
  }

  await supabase.set(async x =>
    x.rpc('updateOrderStatus', {
      orderId: id,
      newTypeId: orderStatusTypeId,
      newPublicStatus: (orderPublicStatus as any).publicStatus,
      newProductsPublicStatus,
      newPrivateStatus: (orderPrivateStatus as any).privateStatus,
      newProductsPrivateStatus,
    }),
  );

  await refresh();
}
</script>
