<template>
  <AppCard
    basis="500px"
    :title="'Учёт заказа #' + props.id"
  >
    <FormKit
      v-if="orderSchema && model"
      ref="form"
      v-model="model"
      type="form"
      :actions="false"
      @submit="onValidSubmit"
    >
      <FormKitSchema :schema="orderSchema" />
      <Button
        v-if="!props.readonly"
        label="Обновить"
        @click="() => onSubmit()"
      />
    </FormKit>
  </AppCard>
</template>

<script setup lang="ts">
import AppCard from '@/components/markup/AppCard.vue';
import {FormKitNode} from '@formkit/core';
import {DateTime} from 'luxon';

const emit = defineEmits<{
  (event: 'update', data: OrderPrivateStatusUpdateData): void;
}>();

const props = defineProps<{
  id: number;
  readonly?: boolean;
  data: OrderPrivateStatusDisplayData;
}>();
const orderSchema = ref(null as null | any[]);
const model = ref(null as null | OrderPrivateStatusDisplayData);
onMounted(() => {
  model.value = {
    deliveryDate: undefinedIfNotDate(props.data.deliveryDate) as any,
    shippingDate: undefinedIfNotDate(props.data.shippingDate) as any,
    notes: props.data.notes,
    totalPriceNet: undefinedIfNotFinite(props.data.totalPriceNet),
    totalPriceGross: undefinedIfNotFinite(props.data.totalPriceGross),
  };

  orderSchema.value = [
    {
      $formkit: 'primeInputNumber',
      name: 'totalPriceNet',
      label: 'Итого цена нетто',
      suffix: ' PLN',
      minFractionDigits: 2,
      maxFractionDigits: 2,
      validation: 'min:0.01|max:1000000000',
      _disabled: true,
    },
    {
      $formkit: 'primeInputNumber',
      name: 'totalPriceGross',
      label: 'Итого цена брутто',
      suffix: ' PLN',
      minFractionDigits: 2,
      maxFractionDigits: 2,
      validation: 'min:0.01|max:1000000000',
      _disabled: true,
    },
    {
      $formkit: 'primeCalendar',
      name: 'deliveryDate',
      label: 'Дата доставки',
      dateFormat: 'yy-mm-dd',
      showButtonBar: true,
      _disabled: props.readonly,
    },
    {
      $formkit: 'primeCalendar',
      name: 'shippingDate',
      label: 'Дата выдачи',
      dateFormat: 'yy-mm-dd',
      showButtonBar: true,
      _disabled: props.readonly,
    },
    {
      $formkit: 'primeTextarea',
      name: 'notes',
      label: 'Заметки',
      rows: '5',
      _disabled: props.readonly,
    },
  ];
});

const form = ref(null as null | {node: FormKitNode});

function onSubmit() {
  form.value?.node.submit();
}

function onValidSubmit() {
  if (!model.value) {
    return;
  }

  const {deliveryDate, shippingDate, notes} = model.value;

  emit('update', {
    deliveryDate: convertDate(deliveryDate),
    shippingDate: convertDate(shippingDate),
    notes,
  });
}
</script>

<script lang="ts">
export interface OrderPrivateStatusUpdateData {
  deliveryDate?: string;
  shippingDate?: string;
  notes?: string;
}

export interface OrderPrivateStatusDisplayData
  extends OrderPrivateStatusUpdateData {
  totalPriceNet?: number;
  totalPriceGross?: number;
}

function undefinedIfNotFinite(num: unknown) {
  if (Number.isFinite(num)) {
    return num as number;
  }

  return undefined;
}

function undefinedIfNotDate(date: unknown) {
  if (typeof date !== 'string') {
    return undefined;
  }

  const result = DateTime.fromFormat(date, 'yyyy-MM-dd');
  
  if (result.isValid) {
    return result.toJSDate();
  }

  return undefined;
}

function convertDate(date: unknown) {
  if (typeof date === 'object' && date instanceof Date) {
    const result = DateTime.fromJSDate(date);

    if (result.isValid) {
      return result.toFormat('yyyy-MM-dd');
    }
  }

  if (typeof date === 'string') {
    const result = DateTime.fromFormat('yyyy-MM-dd', date);

    if (result.isValid) {
      return result.toFormat('yyyy-MM-dd');
    }
  }

  return undefined;
}
</script>
